import 'uni-pages';import '@dcloudio/uni-stat';
import Vue from 'vue'
import App from './App'
import request from './utils/request'
import store from '@/store'

// 把request设置给Vue原型
Vue.prototype.$request = request

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
	...App,
	store
})
app.$mount()